@import url("https://fonts.googleapis.com/css2?family=Lobster+Two:ital@1&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

:root {
  --red: #d01c28;
  --gray: #2e2e2e;
}

* {
  margin: 0;
}

body {
  font-family: "Lobster Two", cursive;
  font-family: "Poppins", sans-serif;
}

button {
  all: unset;
  padding: 6px 16px;
  background: var(--red);
  height: 32px;
  color: white;
  font-weight: 500;
}
.container {
  max-width: 1280px;
  margin: 0 auto;
}

nav {
  display: flex;
  justify-content: space-between;
  height: 72px;
  align-items: center;
}

nav ul {
  display: flex;
  list-style: none;
  gap: 24px;
}



.hero {
  display: flex;
  align-items: center;
  height: calc(100vh - 72px);
}

.hero-content{
    display: flex;
    flex-direction: column;
    gap: 36px;
}

.hero h1 {
  font-weight: 800;
  line-height: 102px;
  font-size: 108px;
}

.hero p{
    font-weight: 600; 
    color: var(--gray);
    max-width: 500px;
}

.hero-btn{
    display: flex;
    gap: 40px;
}

.hero-btn button{
   font-size: 22px;

}

.secondary-btn{
    background: transparent;
    color: var(--gray);
    border: 2px solid gray;
}

.shopping p{
    font-size: 14px;
    margin-bottom: 8px;
    
}

.brand-icons{
    display: flex;
    gap: 16px;
}

@media (max-width:700px) and (min-width:300px){

  main.hero.container{
    height: 550px;
  }
 .hero-content {
    display: flex;
    flex-direction: column;
    gap: 19px;
    margin-left: 12px;
}

.logo{
  margin-left: 10px;
  margin-top: 15px;
}
 nav ul{
    display: flex;
    list-style: none;
    gap: 17px;
    padding-left: 0;
    padding-right: 22px;
    /* align-items: center; */
    /* justify-content: center; */
    /* align-content: center; */
}
.login-btn{
  margin-right: 10px;
}

.hero h1 {
    font-weight: 700;
    line-height: 49px;
    font-size: 42px;
}

.hero p {
    font-weight: 400;
    color: var(--gray);
    max-width: 223px;
    font-size: 9px;
}


.hero-btn {
    display: flex;
    gap: 10px;
}


.hero-btn button {
    font-size: 12px;
    height: 26px;
    width: 62px;
}



.hero-images img {
    height: 242px;
    width: 253px;
    margin-bottom: 120px;
    margin-left: 8px;
}

}